.section_business{
  .subject-box{
    position: relative;
    padding-bottom: 120px;
    margin-bottom: 120px;
    .img-wrap{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .content{
        text-align: center;
        .img{
          margin-bottom: 35px;
        }
        .desc{
          font-size: 22px;
          font-style: normal;
          font-weight: 500;
          line-height: 32px;
          letter-spacing: -0.75px;
          text-align: center;
        }
      }
    }
    .title{
      line-height: 64px;
      margin-bottom: 20px;
    }
    .subject{
      font-size: 18px;
      line-height: 32px;
      color: #000;
      font-weight: 400;
      margin-bottom: 80px;
      &.info{
        margin-bottom: 0;
      }
    }

    .desc{
      word-break: keep-all;
      letter-spacing: -0.75px;
      font-size: 16px;
      line-height: 30px;
      font-weight: 400;
      color: #454545;
    }
    .img{
      margin-bottom: 60px;
      &.caption_bottom{
        margin-bottom: 0px;
      }
    }
    &.no-line{
      padding-bottom: 50px;
      margin: 0;
      &:after{
        display: none;
      }
    }
    &:after{
      content: '';
      display: inline-block;
      border-top: 12px solid #F3F3F3;
      width: 100%;
      position: absolute;
      left: 0;
      bottom: 0;
    }
  }
  .tab-box{
    margin-bottom: 120px;
    .title{
      margin-bottom: 20px;
      line-height: 54px;
    }
    .tab-list{
      display: flex;
      .tab{
        margin-right: 40px;
        a{
          color: #d9d9d9;
          position: relative;
          white-space: nowrap;
          display: inline-block;
          font-weight: bold;
          font-size: 18px;
          height: 38px;
          padding-top: 6px;
        }
        &.active{
          a{
            color: #2E87EF;
            &:after{
              content: '';
              position: absolute;
              width: 100%;
              height: 2px;
              background-color: #2E87EF;
              bottom: 4px;
              left: 0;
            }
          }
        }

      }
    }

  }
  .info-box-wrap{
    &.tab-content{
      .info-box{
        display: none;
        &.show{
          display: block;
        }
      }


    }
  }
  .info-box{
    /*텍스트 관련*/
    .main-title{
      line-height: 54px;
      margin: 0px 0px 60px;
      &.no-desc{
        margin: 0px 0px 80px;
      }

    }
    .subject{
      line-height: 32px;
      font-weight: 500;
      font-size: 18px;
      margin-bottom: 20px;
      letter-spacing: -0.75px;
      &.img_bottom{
        margin-bottom: 60px;
      }
      + .img{
        padding-top: 20px;
      }
    }
    .sub-title{
      margin-bottom: 30px;
      &.figure_bottom{
        margin-bottom: 25px;
      }
      &.graph_bottom{
        margin-bottom: 20px;
      }
    }
    .img{
      + .desc{
        padding-top: 40px;
      }
      + .table{
        margin-top: 80px;
      }
      + .list-area{
        padding-top: 80px;
      }
      + .box-gray-6{
        padding-top: 80px;
      }
      + .list-area-group{
        padding-top: 80px;
      }
    }
    .caption{
      padding-top: 30px;
      font-size: 14px;
      font-weight: 400;
      line-height: 20px;
      letter-spacing: -0.75px;
      color: #909090;
      &.img-caption{
        padding-top: 20px;
        margin-bottom: 40px;
      }
    }
    .desc{
      word-break: keep-all;
      font-weight: 400;
      line-height: 30px;
      letter-spacing: -0.75px;
      color: #454545;
      &.img_bottom{
        margin-bottom: 40px;
      }
      &.list_bottom{
        margin-bottom: 60px;
      }
      &.data_bottom{
        margin-bottom: 60px;
      }
      &.figure_bottom{
        margin-bottom: 25px;
      }
      + .sub-title{
        padding-top: 60px;
      }
    }


    /*리스트 관련*/
    .list-area-group{
      &.figure_bottom{
        margin-bottom: 25px;
      }
      .list-area{
        margin-bottom: 60px;
        &:last-of-type{
          margin-bottom: 0;
        }

      }

      + .main-title{
        padding-top: 60px;
      }
      + .sub-title{
        padding-top: 120px;
      }
    }
    .list-area{
      .sub-list-title{
        font-size: 18px;
        font-style: normal;
        font-weight: 700;
        line-height: 22px;
        margin-bottom: 12px;
      }
      >.title{
        line-height: 27px;
        margin-bottom: 20px;
        &.figure_bottom{
          margin-bottom: 25px;
        }
        &.img_bottom{
          margin-bottom: 35px;
        }
      }
      &.figure_bottom{
        margin-bottom: 25px;
      }
      .figure_bottom{
        margin-bottom: 25px;
      }
    }
    .list-area-border{
      border: 1px solid #d9d9d9;
      margin-bottom: 60px;
      +.sub-title{
        padding-top: 60px;
      }
      .list{
        padding: 40px;
        display: flex;
        &:nth-last-of-type(1){
          border-top: 1px solid #d9d9d9;
        }
        .font-h8{
          flex: 388px 0 0;
        }
        ul{
          li{
            margin-bottom: 2px;
            font-size: 16px;
            font-weight: 400;
            line-height: 30px;
          }
        }
      }

    }
    .list-gallery{
      .list{
        .title{
          line-height: 52px;
          margin-bottom: 40px;
        }
        .content{
          display: flex;
          justify-content: space-between;
          .picture{
            margin-right: 40px;
          }
          .cont{
            .desc{
              margin-bottom: 30px;
              + .btn-wrap{
                padding-top: 20px;
              }
            }
            .caption{
              padding-top: 0;
              margin: -20px 0 30px;
            }
            .list-circle-dot{
              >li{
                color: #707070;
                margin-bottom: 6px;
                &:last-of-type{
                  margin-bottom: 0;
                }
              }


            }
            .btn-wrap{
              padding-top: 50px;
            }
          }
        }
      }
    }


    /*디자인관련*/
    .box-gray-6{
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 40px;
      >div{
        border: 1px solid #d9d9d9;
        width:33.3%;
        padding: 40px;
        &:nth-child(1){
          border-right: none;
          border-bottom: none;
        }
        &:nth-child(2){
          border-right: none;
          border-bottom: none;
        }
        &:nth-child(3){
          border-bottom: none;
        }
        &:nth-child(4){
          border-right: none;
        }
        &:nth-child(5){
          border-right: none;
        }
        .title{
          font-size: 18px;
          font-weight: 700;
          line-height: 22px;
          margin-bottom: 14px;
        }
        .desc{
          color: #666666;
          font-size: 16px;
          word-break: keep-all;
        }
      }
    }
    .border-gray-box-wrap{
      padding-top: 120px;
    }
    .border-gray-box{
      border: 1px solid #e9e9e9;


      img{
        width: 100%;
        height: 100%;
      }
      + .list-area{
        padding-top: 80px;
      }
      + .border-gray-box{
        margin-top: 40px;
      }
      + .no-caption{
        margin-top: 80px;
      }
    }
    .divider{
      margin: 100px 0px;
    }
    .kit-area{
      display: flex;
      flex-wrap: wrap;
      margin-bottom: 40px;
      li{
        text-align: center;
        width: 155.5px;
        border: 1px solid #E9E9E9;
        padding: 20px 30px;
      }
      &:last-of-type{
        margin-bottom: 0;
      }
      &.kit-area-25{
        li{
          &:nth-of-type(1){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(2){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(3){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(4){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(5){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(6){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(7){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(8){
            border-bottom: none;
          }
          &:nth-of-type(9){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(10){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(11){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(12){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(13){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(14){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(15){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(16){
            border-bottom: none;
          }
          &:nth-of-type(17){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(18){
            border-right: none;
            border-bottom: none;
          }
          &:nth-of-type(19){
            border-right: none;
          }
          &:nth-of-type(20){
            border-right: none;
          }
          &:nth-of-type(21){
            border-right: none;
          }
          &:nth-of-type(22){
            border-right: none;
          }
          &:nth-of-type(23){
            border-right: none;
          }
          &:nth-of-type(25){
            border-right: none;
          }

        }
      }
      &.kit-area-8{
        li{
          &:nth-of-type(1){
            border-right: none;
          }
          &:nth-of-type(2){
            border-right: none;

          }
          &:nth-of-type(3){
            border-right: none;

          }
          &:nth-of-type(4){
            border-right: none;

          }
          &:nth-of-type(5){
            border-right: none;
          }
          &:nth-of-type(6){
            border-right: none;
          }
          &:nth-of-type(7){
            border-right: none;
          }
        }
      }

    }
    .file-download{
      margin-bottom: -20px;
    }

    /*테이블 관련*/
    .table-wrap{
      display: flex;

      .table-title-box{
        width: calc(50% - 10px);
        &:nth-of-type(1){
          margin-right: 10px;
        }
        &:nth-of-type(2){
          margin-left: 10px;
        }
        .sub-title{
          margin-bottom: 25px;
        }
      }
      .table{
        &:nth-of-type(1){
          margin-right: 10px;
        }
        &:nth-of-type(2){
          margin-left: 10px;
        }
        thead{
          th{
            padding: 15px;
          }
        }
        tbody{
          td{
            padding: 15px;
          }
        }
      }
    }
    .table{
      margin-bottom: 0;
      thead{
        th{
          padding: 15px 30px;
        }
      }
      tbody{
        td{
          padding: 15px 30px;
        }
      }
      + .sub-title{
        padding-top: 60px;
      }
      + .sub-title.font-h5{
        padding-top: 120px;
      }
      + .list-area-group{
        padding-top: 60px;
      }
    }

    /*버튼*/
    .btn-area{
      display: flex;
      justify-content: center;
      padding-top: 140px;
      + .divider{
        margin:140px auto 80px;
      }
    }
  }
  .notice-text{
    padding-top: 100px;
    line-height: 32px;
    i{
      margin-right: 7px;
    }
    span{
      vertical-align: middle;
      font-size: 18px;
      font-weight: 500;
      letter-spacing: -0.75px;
      color: #909090;
    }
  }
}